.main-nav {
  @media (min-width: @tablets) {
    display: flex;
    align-items: center;
  }

  @media (min-width: @desktops) {
    z-index: 2;
  }
}

.main-nav__items {
  margin: 0;
  padding: 0;
  text-align: center;
  list-style: none;

  @media (min-width: @tablets) {
    display: flex;
    flex-wrap: wrap;
    width: 440px;
  }

  @media (min-width: @desktops) {
    width: 478px;
  }
}

.main-nav__item {
  @media (min-width: @tablets) {
    flex-wrap: wrap;
    margin-right: 35px;
  }

  @media (min-width: @desktops) {
    margin-right: 34px;
  }

  &:nth-child(3n) {
    margin-right: 0;
  }
}

.main-nav__item-link {
  display: block;
  padding: 22px 22px 24px 22px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  color: @black;
  text-transform: uppercase;
  text-decoration: none;
  box-shadow: inset 0 1px 0 @grey_e6;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @tablets) {
    padding: 10px 0 6px;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    border-bottom: 2px solid transparent;
    box-shadow: none;
  }

  @media (min-width: @desktops) {
    padding: 11px 0 5px;
    font-size: 20px;
    line-height: 30px;
  }

  &--current {
    border-color: @green_68;
  }
}

.main-nav--index .main-nav__item-link {
  @media (min-width: @desktops) {
    color: @white;

    &--current {
      border-color: @white;
    }
  }
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
  width: 24px;
  height: 24px;
  padding: 20px;
  background-color: @white;
  border: none;
  z-index: 2;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  @media (min-width: @tablets) {
    display: none;
  }

  &--hidden {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 21px;
  left: 20px;
  width: 24px;
  height: 18px;
  background-image: url("../img/menu-icon-open.svg");
  background-repeat: no-repeat;

  &:active {
    fill: rgba(0, 0, 0, 0.7);
  }
}

.main-nav--closed .main-nav__wrapper {
  @media (max-width: @phones-max) {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 20px;
    left: 22px;
    width: 20px;
    height: 20px;
    background-image: url("../img/menu-icon-close.svg");
    background-repeat: no-repeat;
  }

  &:active::before,
  &:active::after {
    fill: rgba(0, 0, 0, 0.7);
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}

.main-nav--nojs .main-nav__item {
  display: block;
}

.main-nav--nojs .main-nav__item-link--current {
  padding: 22px 22px 24px 22px;
  text-align: center;
}
